export default {
  data: () => ({
    form: {},
    error: null,
  }),

  methods: {
    async register() {
      try {
        await this.$store.dispatch('register/register', this.form)
        return this.$router.push('/signin')
      } catch (e) {
        this.error = e.message
      }
    },
  },
}
