import { axiosUtil } from '@/utils/axios'

/**
 * @type {import('vuex').StoreOptions}
 */
export const transaction = {
  namespaced: true,
  state: () => ({
    transactions: [],
  }),
  mutations: {
    SET_TRANSACTIONS(state, transactions) {
      state.transactions = transactions
    },
  },
  actions: {
    async fetchNotReturnedTransactions({ commit }) {
      const response = await axiosUtil
        .getInstance()
        .get('/book-transaction/available')
      commit('SET_TRANSACTIONS', response.data)
    },

    async fetchHistoryTransactions({ commit }) {
      const response = await axiosUtil
        .getInstance()
        .get('/book-transaction/all-borrow')
      commit('SET_TRANSACTIONS', response.data)
    },
    async fetchAllTransactions({ commit }) {
      const response = await axiosUtil
        .getInstance()
        .get('/book-transaction/all')
      commit('SET_TRANSACTIONS', response.data)
    },
    borrow(ctx, payload) {
      return axiosUtil
        .getInstance()
        .put('/book-transaction/borrow', payload)
        .then((res) => res.data)
    },

    return(ctx, id) {
      return axiosUtil
        .getInstance()
        .put(`/book-transaction/return?id=${id}`)
        .then((res) => res.data)
    },

    endDate(ctx, { job, type }) {
      return axiosUtil
        .getInstance()
        .get(`/book-transaction/end-date?job=${job}&type=${type}`)
        .then((res) => new Date(res.data.limitDate))
    },

    rate(ctx, job) {
      return axiosUtil
        .getInstance()
        .get(`/book-transaction/rate?job=${job}`)
        .then(({ data }) => data)
    },
  },
}
