import { axiosUtil } from '@/utils/axios'

/**
 * @type {import('vuex').StoreOptions}
 */
export const book = {
  namespaced: true,
  state: () => ({
    books: [],
  }),

  mutations: {
    SET_BOOKS(state, books) {
      state.books = books
    },
  },

  actions: {
    async fetchAvaliableBooks({ commit }) {
      const response = await axiosUtil.getInstance().get('/book/avaliable')
      commit('SET_BOOKS', response.data)
    },

    async fetchBook({ state }, id) {
      const book = state.books
        ? state.books.find(({ _id }) => _id === id)
        : await axiosUtil.getInstance().get(`/book?id=${id}`)

      return book
    },
  },
}
