// import auth from '@/middleware/auth'
import auth from '@/middleware/auth'
import { RouteInstance } from '@/utils/route'
import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import RegisterView from '../views/RegisterView.vue'
import SigninView from '../views/SigninView.vue'
import SignoutView from '../views/SignoutView.vue'
import { signin } from '@/middleware/signin'

Vue.use(VueRouter)

const routeInstance = new RouteInstance()

routeInstance.addRoute({
  name: 'home',
  path: '/',
  component: HomeView,
})
routeInstance.addRoute({
  name: 'register',
  path: '/register',
  component: RegisterView,
  meta: {
    title: 'สมัครสมาชิก',
  },
})

routeInstance.addRoute({
  name: 'signin',
  path: '/signin',
  component: SigninView,
  meta: {
    title: 'เข้าสู่ระบบ',
  },

  beforeEnter: signin,
})

routeInstance.addRoute({
  name: 'signout',
  path: '/signout',
  component: SignoutView,
  meta: {
    title: 'ออกจากระบบ',
  },
  beforeEnter: auth('admin', 'user'),
})

routeInstance.addRoute({
  name: 'create-transaction',
  path: '/create-transaction',
  component: () => import('../views/admin/CreateTransactionView.vue'),
  meta: {
    title: 'สร้างรายการยืมหนังสือ',
  },
  beforeEnter: auth('admin'),
})

routeInstance.addRoute({
  name: 'return-transaction',
  path: '/return-transaction',
  component: () => import('../views/admin/ReturnTransactionView.vue'),
  beforeEnter: auth('admin', 'user'),
  meta: {
    title: 'คืนหนังสือ',
  },
})

routeInstance.addRoute({
  name: 'history-transaction',
  path: '/history-transaction',
  component: () => import('../views/transaction/HistoryView.vue'),
  meta: {
    title: 'ประวัติการยืม-คืน หนังสือ',
  },
  beforeEnter: auth('admin'),
})

routeInstance.addRoute({
  name: 'transaction-info',
  path: '/transaction-info',
  component: () => import('../views/transaction/ListView.vue'),
  meta: {
    title: 'รายการยืม-คืน หนังสือ',
  },
  beforeEnter: auth('admin', 'user'),
})

routeInstance.addRoute({
  name: 'user',
  path: '/user',
  component: () => import('../views/EditUserView.vue'),
  meta: {
    title: 'แก้ไขข้อมูลส่วนตัว',
  },
  beforeEnter: auth('admin', 'user'),
})

const routes = routeInstance.getRoutes()

const router = new VueRouter({
  routes,
  mode: 'history',
})

export default router

export { routeInstance }
