import NavigationBar from './components/NavigationBar.vue'
import FooterBar from './components/FooterBar.vue'
import FlowerSet from './components/app/FlowerSet.vue'
import UniverImage from './components/app/UniverImage.vue'
export default {
  components: { NavigationBar, FooterBar, FlowerSet, UniverImage },

  data: () => ({}),

  async beforeMount() {
    const token = this.$cookies.get('access_token')
    if (token) {
      await this.$store.dispatch('me/verifyToken', token)
    }
  },

  computed: {
    isHome() {
      return this.$route.name === 'home'
    },
    title() {
      return this.$route.meta.title
    },

    theme() {
      return this.$vuetify.theme.dark ? 'dark' : 'light'
    },
  },
}
