const tabRoute = {
  home: 0,
  register: 1,
  listTransaction: 2,
  historyTransaction: 3,
  userInfo: 4,
  signOut: 5,
}
export default {
  data: () => ({
    ptabs: null,
  }),
  computed: {
    tabs: {
      get() {
        return tabRoute[this.ptabs]
      },
      set(val) {
        this.ptabs = Object.keys(tabRoute)[val]
      },
    },

    isAdmin() {
      return this.$store.getters['me/GET_USER_ROLE'] === 'admin'
    },

    isLogin() {
      return !!this.$store.state.me.username
    },
  },

  created() {
    const currentRoute = this.$route.name
    this.ptabs = currentRoute
  },
}
