import { axiosUtil } from '@/utils/axios'

const initialState = {
  id: '',
  username: '',
  role: '',
  firstName: '',
  lastName: '',
  faculty: '',
  department: '',
  job: '',
  picture: null,
  borrowed: {},
  remain: {},
}

/**
 * @type {import('vuex').StoreOptions}
 */
const me = {
  namespaced: true,
  state: () => initialState,
  mutations: {
    SET_USER(
      state,
      { id, username, firstName, lastName, faculty, department, role, job }
    ) {
      state.id = id
      state.username = username
      state.firstName = firstName
      state.role = role
      state.lastName = lastName
      state.faculty = faculty
      state.department = department
      state.job = job
    },

    SET_PICTURE(state, picture) {
      state.picture = picture
    },

    SET_BORROW_STATUS(state, borrowStatus) {
      state.borrowed = borrowStatus.borrowed
      state.remain = borrowStatus.remain
    },
  },

  getters: {
    GET_USER_ROLE(state) {
      return state.role
    },
    GET_FULL_NAME(state) {
      return `${state.firstName} ${state.lastName}`
    },

    IS_LOGIN(state) {
      return state.username !== ''
    },

    GET_USER(state) {
      return state
    },

    GET_PICTURE_URL(state) {
      return state.picture ? window.URL.createObjectURL(state.picture) : null
    },
  },
  actions: {
    async signin({ commit }, { username, password }) {
      try {
        const { data } = await axiosUtil.getInstance().post('/auth/sign-in', {
          username,
          password,
        })

        axiosUtil.setToken(data.accessToken)

        commit('SET_USER', data)
        const picture = await axiosUtil
          .getInstance()
          .get('/user/profile-picture', {
            responseType: 'blob',
          })
          .then(({ data }) => data)
        console.log(picture)
        commit('SET_PICTURE', picture)
        return Promise.resolve()
      } catch (e) {
        return Promise.reject(e)
      }
    },

    async signout({ commit }) {
      try {
        await axiosUtil.getInstance().post('/auth/sign-out')
        commit('SET_USER', {})
        return Promise.resolve()
      } catch (e) {
        return Promise.reject(e)
      }
    },
    async verifyToken({ commit }, token) {
      try {
        const decoded = await axiosUtil
          .getInstance()
          .get(`/auth/verify?token=${token}`)
          .then(({ data }) => data)

        axiosUtil.setToken(token)

        commit('SET_USER', decoded)
        const picture = await axiosUtil
          .getInstance()
          .get('/user/profile-picture', {
            responseType: 'blob',
          })
          .then(({ data }) => data)
        commit('SET_PICTURE', picture)
      } catch (e) {
        commit('SET_USER', {
          username: '',
          name: '',
          role: '',
        })
      }
    },

    async fetchBorrowCount({ commit }) {
      const borrowBookStatus = await axiosUtil
        .getInstance()
        .get(`/book-transaction/borrow-remain?type=หนังสือ`)
        .then(({ data }) => data)

      const borrowCDStatus = await axiosUtil
        .getInstance()
        .get(`/book-transaction/borrow-remain?type=ซีดี`)
        .then(({ data }) => data)

      const borrowReservedBookStatus = await axiosUtil
        .getInstance()
        .get(`/book-transaction/borrow-remain?type=หนังสือจอง`)
        .then(({ data }) => data)

      const payload = {
        borrowed: {
          book: borrowBookStatus.borrowed,
          cd: borrowCDStatus.borrowed,
          reservedBook: borrowReservedBookStatus.borrowed,
        },
        remain: {
          book: borrowBookStatus.remain,
          cd: borrowCDStatus.remain,
          reservedBook: borrowReservedBookStatus.remain,
        },
      }

      console.log('ERROR', payload)

      commit('SET_BORROW_STATUS', payload)
    },

    async fetchProfile({ state }) {
      return axiosUtil
        .getInstance()
        .get(`/user?id=${state.id}`)
        .then(({ data }) => data)
    },
  },
}

export { me }
