import store from '../store'
import Vue from 'vue'

/**
 *
 * @param  {...string} roles
 * @returns {import('vue-router').NavigationGuard}
 */
export default function auth(...roles) {
  return async (from, to, next) => {
    if (to.path === 'signin') {
      next()
    }
    store.state.me.role
      ? true
      : await store.dispatch('me/verifyToken', Vue.$cookies.get('access_token'))
    const role = store.state.me.role
    if (!role) {
      next('/signin')
    }
    const valid = roles.includes(role)
    if (!valid) {
      console.log('Called')
      next(from.fullPath)
    }
    next()
  }
}
