import { axiosUtil } from '@/utils/axios'
/**
 * @type {import('vuex').StoreOptions}
 */
export const user = {
  namespaced: true,
  state: {
    users: [],
  },

  mutations: {
    SET_USERS(state, users) {
      state.users = users
    },
  },

  actions: {
    async fetchUsers({ commit }) {
      const response = await axiosUtil.getInstance().get('/user/all')
      commit('SET_USERS', response.data)
    },

    async fetchUser({ state }, id) {
      const user = state.users
        ? state.users.find(({ _id }) => _id === id)
        : await axiosUtil.getInstance().get(`/user?id=${id}`)
      return user
    },
  },
}
