import { axiosUtil } from '../utils/axios'

/**
 * @type {import('vuex').StoreOptions}
 */
export const register = {
  namespaced: true,

  actions: {
    async register(ctx, payload) {
      try {
        await axiosUtil.getInstance().post('/user', payload)
        return Promise.resolve()
      } catch (e) {
        return Promise.reject(e)
      }
    },
  },
}
