export class RouteInstance {
  #routes = []

  /**
   *
   * @param {import('vue-router').RouteConfig} route
   */
  addRoute(route) {
    this.#routes.push(route)
  }

  getRoutes() {
    return this.#routes
  }

  /**
   *
   * @param {string} name Route Name
   * @returns {number} Route Index
   */
  getRouteByName(name) {
    return this.#routes.findIndex((route) => route.name === name)
  }

  /**
   *
   * @param {number} index Route Index
   * @returns Route Name
   */

  getRouteByIndex(index) {
    return this.#routes[index].name
  }

  /**
   *
   * @param {string} name Route Name
   * @returns {string} Route Path
   */
  getPathByRouteName(name) {
    return this.#routes.find((route) => route.name === name).path
  }
}
