export default {
  data: () => ({
    form: {},
    valid: true,
    error: null,
  }),

  methods: {
    login() {
      this.$store
        .dispatch('me/signin', this.form)
        .then(() => {
          this.$router.push('/')
        })
        .catch((e) => {
          this.error = e
        })
    },
  },
}
